<template>
  <!--  -->
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div class="myheader">
        <div>
          <i class="el-icon-star-off"></i>
          <span></span>
        </div>
        <el-button size="small" type="info" plain @click="logout"
          ><i class="el-icon-caret-right el-icon--right"></i
        ></el-button>
      </div>
    </el-header>

    <el-container class="page-component__content">
      <!--  -->
      <el-aside :width="isCollapse ? '79px' : '200px'">
        <el-menu
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="$route.path"
          ref="menuRef"
        >
          <!--  -->
          <div class="collapse" @click="toggleCollapse">
            <i
              :class="isCollapse ? 'el-icon-caret-right' : 'el-icon-caret-left'"
            ></i>
          </div>
          <!--  -->
          <el-submenu
            :index="item.id + ''"
            v-for="item in menus"
            :key="item.id"
          >
            <template slot="title">
              <i :class="iconlist[item.id]"></i>
              <span>{{ item.authName }}</span>
            </template>

            <!--  -->
            <!-- <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id"
                          @click="saveNavState('/' + subItem.path)"> -->
            <el-menu-item
              :index="'/' + subItem.path"
              v-for="subItem in item.children"
              :key="subItem.id"
            >
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>{{ subItem.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <el-backtop
          target="main"
          :bottom="40"
          :right="60"
          :visibility-height="200"
        >
          <div>
            <i class="el-icon-caret-top"></i>
          </div>
        </el-backtop>
        <!-- router-view  keep-alive-->
        <keep-alive
          :exclude="['/login', '/roles', '/users', '/params']"
          :max="20"
        >
          <router-view>welcome</router-view>
        </keep-alive>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
// @ is an alias to /src
export default {
  name: "Home",
  data() {
    return {
      // 
      menus: [],
      // 
      iconlist: {
        125: "el-icon-user",
        103: "el-icon-help",
        101: "el-icon-shopping-bag-1",
        102: "el-icon-s-order",
        145: "el-icon-data-analysis",
      },
      // 
      isCollapse: false,
      // 
      // activePath: ''
    };
  },
  created() {
    this.getMenus();
    // this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    // 
    logout() {
      window.sessionStorage.clear();
      this.$router.push("/login");
      this.$message({
        message: "",
        duration: 1000,
      });
    },
    // 
    async getMenus() {
      const { data: res } = await this.$http.get("/menus");
      if (res.meta.status !== 200)
        return this.$message.error({
          message: res.meta.msg,
          duration: 1000,
        });
      this.menus = res.data;
      // console.log(res);
    },
    // 
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
    // 
    // saveNavState(activePath) {
    //   window.sessionStorage.setItem('activePath', activePath)
    //   this.activePath = activePath
    // }
    // $route.path
    // index
    // handleOpen(key) {
    //   window.sessionStorage.setItem('index', key)
    //   console.log(key);
    // }
  },
  watch: {
    $route() {
      if (this.$route.path == "/welcome") {
        // const i = window.sessionStorage.getItem('index')
        // this.$refs.menuRef.closeMenu(i)
        const arr = ["125", "103", "102", "101", "145"];
        arr.forEach((i) => {
          this.$refs.menuRef.close(i);
        });
        // console.log(i);
        // console.log(this.$refs.menuRef);
      }
    },
  },
};
</script>

