<template>
  <div class="login-page">
    <div class="login-box">
      <!--  -->
      <div class="login-avatar">
        <img src="../assets/images/logo.png" alt="avatar">
      </div>
      <!--  -->
      <el-form ref="formRef" :rules="loginRules" :model="loginForm" label-width="0px" class="login-form">
        <!--  -->
        <el-form-item prop="username">
          <el-input placeholder="" type="text" v-model="loginForm.username" prefix-icon="el-icon-user-solid"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input placeholder="" show-password v-model="loginForm.password" prefix-icon="el-icon-lock"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button @click="login" type="primary"></el-button>
          <el-button @click="resetForm" type="info"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      // 
      loginForm: {
        username:'admin',
        password:'123456'
      },
      // 
      loginRules: {
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: ' 3 ~ 10 ', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 12, message: ' 6 ~ 12 ', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 
    resetForm() {
      this.$refs.formRef.resetFields()
    },
    // 
    login() {
      this.$refs.formRef.validate(async valid => {
        // console.log(valid);
        if(!valid)return;
        const {data: res} = await this.$http.post('login',this.loginForm);
        if(res.meta.status !== 200) return this.$message.error({
          message: res.meta.msg,
          duration: 1000
        });
        this.$message.success({
          message: res.meta.msg,
          duration: 1000
        })
        // console.log(res);
        // token
        window.sessionStorage.setItem('token',res.data.token);
        // 
        this.$router.push('/home')
      })
    }
  }

}
</script>

<style lang="less" scoped>
  .login-page {
    display: flex;
    height: 100%;
    background: url(https://cdn.jsdelivr.net/gh/SudaLWC/picTest@1.0/bgc.jpg);
    background-size: cover;
  }
  .login-box {
    margin: auto;
    width: 450px;
    height: 300px;
    background-color: rgba(255, 255, 255, .3);
    border-radius: 10px;
    box-shadow: 0 0 2px rgb(90, 145, 155);
    
    .login-avatar {
      margin: -3px auto;
      height: 50px;
      width: 100px;
      img {
        height: 100%;
        width: 100%;
      }
    }
    .login-form {
      padding: 0 60px;
      margin-top: 50px;
      .btns {
        display: flex;
        justify-content: flex-end;
      }
    }
  }
</style>